'use client'

import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import * as React from 'react';

interface Doc {
    pageContent?: string;
    metadata?: {
        loc?: {
            pageNumber?: number;
        };        
        source?: string;
    }
}

interface IMessage {
    role: 'assistant' | 'user';
    content?: string;
    documents?: Doc[]; 
}

const ChatScreen: React.FC = () => {

    const [placeholder, setPlaceholder] = React.useState<string>('Enter your query here')
    const [message, setMessage] = React.useState<string>('');
    const [messages, setMessages] = React.useState<IMessage[]>([]);

    const handleSendChat = async () => {
        setMessages((prev) => [...prev, {role:'user', content:message}]);
        const res = await fetch(`http://localhost:8000/chat?message=${message}`);
        const data = await res.json();
        console.log({data});
        setMessage('');
        setPlaceholder('Enter your query here');
        setMessages(prev => [...prev, {role: 'assistant', content: data?.message}]);

    }
    
    return (
        <div className='p-4'>
            <div>
                {messages.map((message, index) => <pre key={index}>{JSON.stringify(message, null, 2)}</pre>)}
            </div>
            <div className='fixed bottom-4 w-100 flex gap-3'>
                <Input value={message} onKeyDown={e => {if (e.key === 'Enter' && message.trim()) {handleSendChat()} }} onChange={e => setMessage(e.target.value)} placeholder='Enter your query here' />
                <Button onClick={handleSendChat} disabled={!message.trim()} className='bg-black text-white'>Send</Button>
            </div>
        </div>

    );

}

export default ChatScreen;